function TodoForm({ addTodo }) {

  const handleSubmit = (e) => {
    e.preventDefault()
    if (!value) return
    props.addTodo(value)
  }

  return (
    <form onSubmit={handleSubmit}>
      <input
        type="text"
        className="input"
        onChange={(e) => setValue(e.target.value)}
      />
      <button type="submit">Submit</button>
    </form>
  )
}

export default TodoForm
